import React, {Component} from 'react';
import { withRouter } from 'react-router-dom';
import loanService from '../../../../services/LoanService'
import LoanDetailHeader from './LoanDetailHeader'
import LoanSchedule from './LoanSchedule';


class LoanDetails extends Component {
    constructor(props) {
        super(props)
        this.state = {
            loan: {
                details: {}
            },
        }
        this.loanService = new loanService()
    }

    componentDidMount = async () => {
        this.loader()
    }

    loader = async () => {
        const { loanId } = this.props
        let loan = await this.loanService.getLoanCompleteDetails(loanId)
        this.setState({ loan })
    }


    compare = (a,b) =>  {
        if (a.date < b.date)
          return -1;
        if (a.date > b.date)
          return 1;
        return 0;
    }


    render() {
        let { details} = this.state.loan
        return (
            (this.state.loan.details._borrower) ?
                (
                    <div className="content">
                            <LoanDetailHeader details={details}/>
                        <div className="loan-content-holder">
                            <LoanSchedule loanSchedule={details.loanSchedule.sort(this.compare)} />
                        </div>
                    </div>
                ) :
                "")
    }
}


  
  export default withRouter(LoanDetails);